
import { marked } from 'marked';

const HtmlToReactParser = require('html-to-react').Parser;
const markdown = require("markdown-wasm/dist/markdown.es")
const JSONfn = require("json-fn")


export const processMarkdownWithWorker = async (markdownData: string) => {
    if(markdownData.length == 0 ) {
        return ["", ""]
    }
    const htmlToReactParser = new HtmlToReactParser();
    await markdown.ready
      // @ts-ignore: Unreachable code error
    var rawMarkup = marked.parse(markdownData);
    const parsed = htmlToReactParser.parse(rawMarkup)
    return [JSONfn.stringify(parsed), rawMarkup];
}